


























import Vue from 'vue';
import Component from 'vue-class-component';
import RegisterGroupUser from "@/components/RegisterGroupUser.vue";

@Component({
    components: {
        RegisterGroupUser
    }
})
export default class Group extends Vue {
    public src: string = '';

    public form: any = {
        firstName: null,
        lastName: null,
        gender: null,
        age: null,
        email: null,
    };

    public data() {
        return {
            src: undefined,
            form: {
                firstName: null,
                lastName: null,
                gender: null,
                age: null,
                email: null,
            },

        };
    }

    public getValidationClass(test: any): string {
        return ""
    }

    public created(): void {
        this.src =
            'https://groups.google.com/forum/embed/?place=forum/jadzwingow2' +
            '&showsearch=true&showpopout=true&showtabs=false' +
            '&parenturl=' +
            encodeURIComponent(window.location.href);
    }
}
