























import Vue from 'vue';
import Component from 'vue-class-component';
import 'vue-material/dist/vue-material.min.css';
import 'vue-material/dist/theme/default.css';
import WorkerService from "@/service/worker/WorkerService";
import {Watch} from "vue-property-decorator";
import NotificationServcice from "@/service/motification/NotificationServcice";


@Component({
})
export default class App extends Vue {

    private menuVisible: boolean = false;
    private enableNotification: boolean = false;
    private enableNotificationMutable: boolean = false;

    private notificationAvailable: boolean = false;
    private skipNextEnableNotificationWatch: boolean = false;

    private notificationService?: NotificationServcice;

    private created() {
        let workerService: WorkerService = new WorkerService(this.$store);
        workerService.init().then(() => {
            let notificationService = new NotificationServcice(workerService);
            this.notificationService = notificationService;

            notificationService.init().then(value => {
                if (this.enableNotification != notificationService.isEnabled)
                    this.skipNextEnableNotificationWatch = true;

                this.enableNotification = notificationService.isEnabled;
                this.enableNotificationMutable = notificationService.isEnabled;
                this.notificationAvailable = notificationService.isAvailable;
            })
        });

    }

    private data() {
        return {
            menuVisible: false,
            enableNotification: false
        };
    }

    @Watch("enableNotification")
    private onNotificationSwitchToggle() {
        console.debug("onNotificationSwitchToggle");

        if (!this.skipNextEnableNotificationWatch) {
            console.debug("onNotificationSwitchToggle1");

            if (!this.enableNotificationMutable && this.notificationService != null) {
                this.notificationService.enableNotifications().catch(reason => {

                    this.skipNextEnableNotificationWatch = true;
                    this.enableNotification = false

                }).then(value => {
                    this.enableNotificationMutable = true;
                    this.enableNotification = true

                })
            } else {
                if (this.enableNotificationMutable && this.notificationService != null) {
                    this.notificationService.disableNotifications().catch(reason => {
                        this.skipNextEnableNotificationWatch = true;
                        this.enableNotification = true
                    }).then(value => {
                        this.enableNotificationMutable = false;
                        this.enableNotification = false

                    })

                }
            }


        }
        this.skipNextEnableNotificationWatch = false;

    }
}
